<template>
  <div class="mvs-container">
      <div class="filter-wrap">
        <div class="section-wrap">
          <!-- 分类切换 地区 -->
          <span class="section-type">地区:</span>
          <ul class="tabs-wrap">
            <li class="tab">
              <span 
              class="title active" 
              >全部</span>
            </li>
             <li class="tab">
              <span 
              class="title" 
              >内地</span>
            </li>
          </ul>
        </div>

        <div class="type-wrap">
          <span class="type-type">类型:</span>
          <ul class="tabs-wrap">
            <li class="tab">
              <span 
              class="title" 
              >全部</span>
            </li>
            <li class="tab">
              <span 
              class="title" 
              >官方版</span>
            </li>
          </ul>
        </div>

        <div class="order-wrap">
          <span class="order-type">排序:</span>
          <ul class="tabs-wrap">
            <li class="tab">
              <span 
              class="title" 
              >上升最快</span>
            </li>
            <li class="tab">
              <span 
              class="title" 
              >最热</span>
            </li>
          </ul>
        </div>
      </div>
      <!-- mv容器 -->
      <!-- 推荐mv -->
      <div class="mvs">
        <div class="items">
          <div class="item" @click="toMv(item.id)" v-for="(item,index) in list" :key="index">
            <div class="img-wrap">
              <img :src="item.cover" alt="">
              <div class="num-wrap">
                <div class="iconfont icon-play"></div>
                <div class="num">{{item.playCount}}</div>
              </div>
            </div>
            <div class="info-wrap">
              <div class="name"> {{item.name}}</div>
              <div class="singer">{{item.artistName}}</div>
            </div>
          </div>
        </div>
         <!-- 分页器 -->
      <el-pagination
        background
        layout="prev, pager, next"
        :total="100"
        :size="10"
      ></el-pagination>
      </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 总条数
      total: 20,
      // 页码
      page: 1,
      // 页容量
      limit: 8,
      // 地区
      area: '全部',
      // 类型
      type: '全部',
      // 排序
      order: '上升最快',
      // 查询的结果
      list: []
    }
  },
  created () {
    // 获取数据
    this.getList()
  },
  methods: {
    getList () {
      // 获取数据
      this.FFaxios({
        url: 'https://autumnfish.cn/mv/all',
        method: 'get',
        params: {
          area: this.area,
          type: this.type,
          order: this.order,
          // 数量
          limit: this.limit,
          // 偏移值 分页 (页码-1)*数量
          offset: (this.page - 1) * this.limit
        }
      }).then(res => {
        // console.log(res)
        this.list = res.data.data
        // 处理次数
        for (let i = 0; i < this.list.length; i++) {
          if (this.list[i].playCount > 100000) {
            this.list[i].playCount =
              parseInt(this.list[i].playCount / 10000) + '万'
          }
        }
        // 保存总条数
        // 接口问题 有count 就赋值
        if (res.data.count) {
          this.total = res.data.count
        }
      })
    },
    toMv (id) {
      this.$router.push(`/mv?id=${id}`)
    }
  }
}
</script>
<style lang='scss'>
.mvs-container {
  padding-top: 20px;
  .filter-wrap {
    > div {
      margin-bottom: 30px;
      vertical-align: baseline;
      display: flex;
      align-items: center;
      span {
        font-size: 14px;
        height: 100%;
        &.title {
          margin: 20px;
          color: gray;
          cursor: pointer;
          padding: 5px 20px;
          &::before {
            content: "", pos;
          }
        }
        &.active {
          color: #dd6d60;
          background-color: #fcf6f5;
          border-radius: 20px;
        }
      }
      ul {
        display: flex;
        > li:not(:last-child) {
          border-right: 10px solid #f2fef1;
        }
      }
    }
  }
  .mvs {
    .items {
      display: flex;
      justify-content: flex-start;
      flex-wrap: wrap;
      .item {
        cursor: pointer;
        margin-right: 25px;
        margin-bottom: 30px;
        margin-left: 0;
        width: 250px;
        .img-wrap {
          position: relative;
          width: 100%;
          img {
            border-radius: 5px;
            width: 100%;
          }
          .num-wrap {
            position: absolute;
            color: white;
            top: 0;
            right: 0;
            display: flex;
            align-content: center;
            font-size: 15px;
            padding-right: 5px;
            padding-top: 2px;
            .icon-play {
              font-size: 12px;
              display: flex;
              align-items: center;
              margin-right: 5px;
            }
          }
        }
        .info-wrap {
          .name {
            font-size: 15px;
          }
          .singer {
            font-size: 14px;
            color: gray;
          }
        }
      }
    }
    .el-pagination {
      margin-top: 50px;
      text-align: center;
    }
  }
}
</style>